body {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  margin: 0;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  margin: 0;
}

#header-div {
  display: flex;
  height: 100px;
  width: 100vw;
  align-items: center;
  justify-content: space-around;
  background: linear-gradient(
    to right,
    white,
    white 33.33%,
    lightgray 33.33%,
    lightgray 66.66%,
    white 66.66%
  );
}

.header-btn {
  width: 30px;
  text-align: center;
  font-size: x-small;
  display: flex;
  justify-content: center;
  align-items: center;
}

#gallery-div {
  height: 250px;
  width: 100vw;
  overflow: auto;
  overflow-y: hidden;
  display: flex;
}

.gallery-img {
  height: 250px;
  width: 250px;
}

#content-div {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
}

#text-div {
  width: 100vw;
  position: relative;
  overflow-y: auto;
  word-wrap: break-word;
  display: flex;
  flex-grow: 1;
}

#text {
  position: absolute;
  padding: 10px;
}

#footer-div {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  height: 100px;
  width: 100vw;
}

#name {
  margin: 10px;
}

